import React, {Suspense} from 'react';
import {Route,Switch,Redirect} from "react-router-dom";
import {useSelector} from "react-redux"
import { CircularProgress } from '@material-ui/core';

const Drivers = React.lazy(() => import('../Drivers/DriversContainer'));
const Dashboard = React.lazy(() => import("../Dashboard/Dashboard"));
const Riders  = React.lazy(()=>import("../Riders/RidersContainer"));
const Trips = React.lazy(()=>import("../Trips/TripsContainer"));
const Vehicles = React.lazy(()=> import("../Vehicles/VehiclesContainer"));
const ServiceAreas = React.lazy(()=> import("../Service_Areas/ServiceAreasContainer"));
 const Router =(props)=>  {
    const isLogged = useSelector(state=>state.auth.isLogged)
        return (
        
                <Suspense fallback={<div style={{display:"flex",justifyContent:"center",padding:"20px"}}>
                        <CircularProgress/>
                        </div>}>
                <Switch>
                {isLogged? <Route path="/home" component={Dashboard} />:null}
                {isLogged?<Route path="/drivers" component={Drivers} />:null}
                {isLogged?<Route path="/riders" component={Riders}/>:null}
                {isLogged?<Route path="/rides" component={Trips}/>:null}
                {isLogged? <Route path="/vehicles" component={Vehicles} />:null}
                {isLogged? <Route path="/service_areas" component={ServiceAreas} />:null}
                {isLogged? <Route exact path="/"><Redirect to="/home" /></Route>:null}
                </Switch>
                </Suspense>
        
        
                  
        )
    
}

export default Router
